import common from "./common";
import apiRequest from "../request/api/ApiRequest";

const dataSave = {


  saveEditData (documentList) {
    const that = this
    apiRequest.getData().then(data => {
      data.documentList = documentList
      for (const document of documentList) {
        data.tagList = that.tagListHandler(data.tagList, document)
      }
      // TODO: 2022/7/30 保存session
    })
  },

  saveDataToFile (documentList) {
    const that = this
    apiRequest.getData().then(data => {
      data.documentList = documentList
      for (const document of documentList) {
        data.tagList = that.tagListHandler(data.tagList, document)
      }
      common.saveFile('data.json', JSON.stringify(data))
    })
  },


  tagListHandler (tagList, document) {
    if (tagList.length > 0) {
      for (const tag of document.tagList) {
        let tagInfo = this.tagListSearchHandler(tagList, tag)
        if (tagInfo) {
          tagInfo.documentIdList.push(document.id)
        } else {
          tagList.push({name: tag, documentIdList: [document.id]})
        }
      }
    } else {
      for (const tag of document.tagList) {
        tagList.push({name: tag, documentIdList: [document.id]})
      }
    }
    return tagList
  },


  tagListSearchHandler(tagList, name) {
    for (const tag of tagList) {
      if (tag.name === name) {
        return tag
      }
    }
    return undefined
  },


  deleteDocumentInfo (documentId) {
    console.log(documentId)
    apiRequest.queryData().then(res => {
      let document = {}
      for (let i = 0; i < res.documentList.length; i++) {
        if (res.documentList[i].id === documentId) {
          document = res.documentList[i]
          res.documentList.splice(i, 1)
        }
      }
      for (const tag of res.tagList) {
        if (document.tagList.indexOf(tag.name) !== -1) {
          let index = tag.documentIdList.indexOf(documentId)
          tag.documentIdList.splice(index, 1)
        }
      }
      common.saveFile('data.json', JSON.stringify(res))
    })
  },


  editDocumentInfo (document) {
    apiRequest.queryData().then(res => {
      let oldDocument = {}
      for (let i = 0; i < res.documentList.length; i++) {
        let item = res.documentList[i]
        if (item.id === document.id) {
          oldDocument = item
          res.documentList.splice(i, 1)
          res.documentList.push(document)
          break
        }
      }
      for (const tag of res.tagList) {
        if (oldDocument.tagList.indexOf(tag.name) !== -1) {
          let index = tag.documentIdList.indexOf(document.id)
          tag.documentIdList.splice(index, 1)
        }
      }
      for (const tag of res.tagList) {
        let index = document.tagList.indexOf(tag.name)
        if (index !== -1) {
          tag.documentIdList.push(document.id)
          document.tagList.splice(index, 1)
        }
      }
      for (const tag of document.tagList) {
        res.tagList.push({name: tag, documentIdList: [document.id]})
      }
      common.saveFile('data.json', JSON.stringify(res))
    })

  },


  addDocumentInfo (documentInfo) {
    apiRequest.queryData().then(res => {
      res.tagList = this.tagDuplicateRemoval(res.tagList, documentInfo.tagList, documentInfo.id)
      res.documentList.push(documentInfo)
      common.saveFile('data.json', JSON.stringify(res))
    })
  },


  tagDuplicateRemoval (tagList, tags, documentId) {
    if (tagList && tagList.length > 0) {
      for (const tag1 of tagList) {
        for (const tag2 of tags) {
          if (tag1.name === tag2) {
            tag1.documentIdList.push(documentId)
          }
        }
      }
      for (const tag of tags) {
        tagList.push({name: tag, documentIdList: [documentId]})
      }
    } else {
      for (const tag of tags) {
        tagList.push({name: tag, documentIdList: [documentId]})
      }
    }
    return tagList
  }

}

export default dataSave
