import http from "../http";

const apiRequest = {


  getData () {
    return http.get('/luochen/data', {})
  },

  getDocumentDetails (id) {
    return http.get('/luochen/document/details', {id: id})
  },

  getDocumentList (params) {
    return http.get('/luochen/document/list', params)
  },

  getRecomDocumentList () {
    return http.get('/luochen/document/recom/list', {})
  },

  getTagList () {
    return http.get('/luochen/tag/list', {})
  },

  getNewDocumentList () {
    return http.get('/luochen/document/new/list', {})
  },

  getDocumentStatistics () {
    return http.get('/luochen/document/statistics', {})
  },

  getNavigationList () {
    return http.get('/luochen/navigation/list', {})
  }

}

export default apiRequest
