<template>
  <div>
    <backend-title-bar>
      <span slot="title">添加文章</span>

      <a-button slot="action1" type="primary" style="width: 100%;">
        <a-icon type="save"/> <span>保存</span>
      </a-button>

      <div slot="action2">
        <a-tooltip placement="left">
          <div slot="title">
            <span v-if="documentList.length === 0">你还没有添加文章, 快去试试创建一篇文章吧</span>
            <div v-if="documentList.length > 0">
              <div class="save-document-list">
                <div v-for="(item, index) in documentList" :key="index">
                  <span class="prefix">{{index+1}}</span>
                  <span class="text">{{ item.title }}</span>
                  <span class="suffix" @click="deleteDocumentListHandler(index)">
                      <a-icon type="delete"/>
                    </span>
                </div>
              </div>
            </div>
          </div>
          <a-button type="primary" style="width: 100%;" @click="saveDocumentListHandler">
            <a-icon type="export"/> <span>导出({{documentList.length}})</span>
          </a-button>
        </a-tooltip>
      </div>
    </backend-title-bar>

    <div class="document-add-steps-box">
      <a-steps :current="tabsActiveKey">
        <a-step title="创建文章基本信息"/>
        <a-step title="创建文章正文信息"/>
        <a-step title="预览文章信息"/>
      </a-steps>
    </div>

    <!-- 基本信息 -->
    <div v-show="tabsActiveKey === 0">
      <div class="document-add-body-box">

        <div class="document-add-body-item">
          <a-row>
            <a-col :span="2">
              <div class="document-add-body-item-title">文章ID：</div>
            </a-col>
            <a-col :span="22">
              <div class="document-add-body-item-input">{{documentInfo.id}}</div>
            </a-col>
          </a-row>
        </div>

        <div class="document-add-body-item">
          <a-row>
            <a-col :span="2">
              <div class="document-add-body-item-title">文章标题：</div>
            </a-col>
            <a-col :span="22">
              <a-input size="large" v-model="documentInfo.title"/>
            </a-col>
          </a-row>
        </div>

        <div class="document-add-body-item">
          <a-row>
            <a-col :span="2">
              <div class="document-add-body-item-title">文章标签：</div>
            </a-col>
            <a-col :span="10">
              <a-select mode="tags" style="width: 100%" size="large" :defaultValue="[]"
                        v-model="documentInfo.tagList" placeholder="选择文章标签" @change="handleChange">
                <a-select-option v-for="tag in tagList" :key="tag.name">
                  {{ tag.name }}
                </a-select-option>
              </a-select>
            </a-col>
            <a-col :span="3">
              <div class="document-add-body-item-title">创建时间：</div>
            </a-col>
            <a-col :span="9">
              <div class="document-add-body-item-input">{{timestamp(documentInfo.datetime)}}</div>
            </a-col>
          </a-row>
        </div>

        <div class="document-add-body-item">
          <a-row>
            <a-col :span="2">
              <div class="document-add-body-item-title">阅读：</div>
            </a-col>
            <a-col :span="6">
              <a-input size="large" v-model="documentInfo.action.read"/>
            </a-col>
            <a-col :span="2">
              <div class="document-add-body-item-title">点赞：</div>
            </a-col>
            <a-col :span="6">
              <a-input size="large" v-model="documentInfo.action.like"/>
            </a-col>
            <a-col :span="2">
              <div class="document-add-body-item-title">评论：</div>
            </a-col>
            <a-col :span="6">
              <div class="document-add-body-item-input">{{documentInfo.action.comment}}</div>
            </a-col>
          </a-row>
        </div>

        <div class="document-add-body-item">

          <a-row>
            <a-col :span="2">
              <div class="document-add-body-item-title">封面：</div>
            </a-col>
            <a-col :span="8">
              <a-input size="large" v-model="documentImg" @blur="addDocumentImgHandler"/>
            </a-col>
            <a-col :span="13" :offset="1">
              <div class="document-img-box">
                <div class="document-img-info" v-show="documentInfo.img !== ''">
                  <img :src="documentInfo.img" alt=""/>
                  <div class="document-img-text">
                    <a-icon type="delete" @click="deleteDocumentImgHandler"/>
                  </div>
                </div>
                <div class="document-img-x-info" v-show="documentInfo.img === ''">
                  <a-icon type="interaction"/>
                </div>
              </div>
            </a-col>
          </a-row>

        </div>

        <div class="document-add-body-next-btn-box">
          <a-row>
            <a-col :span="3" :offset="21">
              <a-button type="primary" size="large" @click="nextDocumentHandler(1)"
                        style="width: 100%;">创建文章正文</a-button>
            </a-col>
          </a-row>
        </div>

      </div>
    </div>
    <!-- / 基本信息 -->

    <!-- 正文信息 -->
    <div  v-show="tabsActiveKey === 1">

      <div class="document-add-body-editor-box">

        <div class="full-screen-container">
          <div id="toolbar-container"></div>
          <div id="editor-container"></div>
        </div>

        <div class="document-add-body-next-btn-box">
          <a-row>
            <a-col :span="3">
              <a-button type="primary" size="large" @click="nextDocumentHandler(0)"
                        style="width: 100%;">返回上一步</a-button>
            </a-col>
            <a-col :span="3" :offset="14">
              <a-button type="primary" size="large" @click="nextDocumentHandler(2)"
                        style="width: 100%;">预览文章</a-button>
            </a-col>
            <a-col :span="3" :offset="1">
              <a-button type="primary" size="large" @click="saveDocumentInfoHandler"
                        style="width: 100%;">添加文章</a-button>
            </a-col>
          </a-row>
        </div>
      </div>

    </div>
    <!-- / 正文信息 -->

    <!-- 预览 -->
    <div v-show="tabsActiveKey === 2">

      <div class="document-add-body-editor-box">
        <div class="preview-box">
          <div class="title"># {{ documentInfo.title }}</div>
          <div v-highlight v-html="documentInfo.content"></div>
        </div>
        <div class="document-add-body-next-btn-box">
          <a-row>
            <a-col :span="3">
              <a-button type="primary" size="large" @click="nextDocumentHandler(1)"
                        style="width: 100%;">返回上一步</a-button>
            </a-col>
            <a-col :span="3" :offset="18">
              <a-button type="primary" size="large" @click="saveDocumentInfoHandler"
                        style="width: 100%;">添加文章</a-button>
            </a-col>
          </a-row>
        </div>
      </div>

    </div>
    <!-- / 预览 -->

  </div>
</template>

<script>
  import BackendTitleBar from "../../../components/backend/BackendTitleBar";
  import common from "../../../util/common";
  import 'highlight.js/styles/monokai-sublime.css'
  import hljs from 'highlight.js'
  import dataSave from "../../../util/data-save";
  import dataCache from "../../../util/data-cache";
  import apiRequest from "../../../request/api/ApiRequest";

  const { createEditor, createToolbar } = window.wangEditor
  let editor = undefined
  export default {
    name: "BackendAddDocument",
    data () {
      return {
        tabsActiveKey: 0,
        documentList: [],
        tagList: [],
        documentImg: '',
        documentInfo: {
          id: common.getuuid(),
          title: '',
          img: '',
          status: true,
          label: '',
          tagList: [],
          tag: '',
          abstract: '',
          action: {
            read: Math.ceil((Math.random() * 10000) + 200),
            like: Math.ceil((Math.random() * 10000) + 200),
            comment: 0
          },
          datetime: new Date().getTime(),
          catalogue: [],
          content: ''
        }
      }
    },
    mounted() {
      apiRequest.getTagList({page: 1, rows: 10}).then(res => {
        if (res.code === 2000) {
          this.tagList = res.data
        }
      })
      this.createEditor()
    },
    methods: {
      createEditor () {
        const editorConfig = {}
        editor = createEditor({
          selector: '#editor-container',
          config: editorConfig,
          mode: 'default'
        })
        editor.highlight = hljs
        const toolbarConfig = {}
        createToolbar({
          editor,
          selector: '#toolbar-container',
          config: toolbarConfig,
          mode: 'default'
        })

      },
      timestamp (timestamp) {
        return common.timestamp(timestamp)
      },
      handleChange() {
        if (this.documentInfo.tagList.length > 3) {
          this.$message.warning("文件标签最多只能选择3个")
          this.documentInfo.tagList.splice(3, 1)
        }
      },
      nextDocumentHandler (key) {
        if (key === 1) {
          if (this.documentInfo.title.length < 1) {
            this.$message.warning('文章标题为必填项')
            return
          }
          if (this.documentInfo.img.length < 1) {
            this.$message.warning('文章封面为必填项')
            return
          }
        }
        this.tabsActiveKey = key
        this.saveDocumentInfoHandler(false)
      },
      addDocumentImgHandler () {
        this.documentInfo.img = this.documentImg
      },
      deleteDocumentListHandler (index) {
        this.documentList.splice(index, 1)
      },
      deleteDocumentImgHandler () {
        this.documentInfo.img = ''
        this.documentImg = ''
      },
      saveDocumentListHandler () {
        if (this.documentList.length === 0) {
          this.$message.warning('未添加文章信息, 无法保存')
        } else {
          dataSave.saveDataToFile(this.documentList)
        }
      },
      saveDocumentInfoHandler (save) {
        if (editor) {
          this.documentInfo.catalogue = editor.getElemsByTypePrefix('header')
          this.documentInfo.abstract = editor.getText()
          // eslint-disable-next-line no-undef
          this.documentInfo.content = $('.w-e-scroll').html()

          if (save) {
            this.documentList.push(JSON.parse(JSON.stringify(this.documentInfo)))
            dataCache.addDocumentList(this.documentList)
            this.documentInfo = {
              id: common.getuuid(), title: '', img: '', tagList: [], abstract: '', tag: '',
              action: {read: Math.ceil((Math.random() * 10000) + 200), like: Math.ceil((Math.random() * 10000) + 200), comment: 0},
              datetime: new Date().getTime(), catalogue: [], content: ''
            }
            editor.clear()
            this.documentImg = ''
            this.tabsActiveKey = 0
          }
        } else {
          this.$message.error('创建富文本编辑器失败')
        }
      }
    },
    components: { BackendTitleBar }
  }
</script>

<style scoped>
.save-document-list{
  width: 200px;
  height: 200px;
  overflow-y: scroll;
}
.save-document-list::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.save-document-list::-webkit-scrollbar-thumb {
  background: none;
}
.save-document-list::-webkit-scrollbar-track {
  background: none;
}
.save-document-list div{
  width: 200px;
  height: 22px;
  margin: 4px 0;
  font-size: 13px;
  border-bottom: #007aff dashed 1px;
}
.save-document-list div .prefix{
  float: left;
  display: block;
  width: 18px;
  height: 18px;
  text-align: center;
  line-height: 18px;
  border-radius: 2px;
  margin-right: 6px;
  background: rgba(255, 255, 255, .6);
}
.save-document-list div .text{
  float: left;
  display: block;
  width: 155px;
  height: 21px;
  line-height: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.save-document-list div .suffix{
  float: left;
  display: block;
  width: 18px;
  height: 18px;
  text-align: center;
  line-height: 18px;
  border-radius: 2px;
  cursor: pointer;
  transition: var(--transition);
}
.save-document-list div .suffix:hover{
  color: #ff4d4f;
}
.document-add-steps-box{
  width: 80%;
  margin: 20px auto;
}
.document-add-body-box{
  width: 80%;
  margin: 20px auto;
  padding: 20px;
  box-shadow: var(--box-shadow);
  border-radius: var(--border-radius);
}
.document-add-body-box .document-add-body-item{
  margin-bottom: 10px;
}
.document-add-body-box .document-add-body-item textarea{
  resize: none;
}
.document-add-body-box .document-add-body-item-title{
  text-align: right;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  color: var(--color);
}
.document-add-body-box .document-add-body-item-input{
  box-sizing: border-box;
  margin: 0;
  font-variant: tabular-nums;
  font-feature-settings: 'tnum';
  position: relative;
  display: inline-block;
  width: 100%;
  height: 40px;
  padding: 6px 11px;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.65);
  line-height: 1.5;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #d9d9d9;
}
.document-img-box{
  position: relative;
  width: 130px;
  height: 130px;
  border-radius: 6px;
  border: 1px dashed #d9d9d9;
}
.document-img-box .document-img-info{
  position: absolute;
  width: 90%;
  height: 90%;
  overflow: hidden;
  border-radius: 6px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.document-img-box .document-img-info img{
  width: 100%;
  height: 100%;
  z-index: 9;
}
.document-img-box .document-img-info .document-img-text{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  line-height: 130px;
  color: #fff;
  border-radius: 6px;
  background: rgba(0, 0, 0, .4);
  z-index: 10;
  cursor: pointer;
  opacity: 0;
  transition: var(--transition);
}
.document-img-box .document-img-info .document-img-text:hover{
  opacity: 1;
}
.document-img-box .document-img-x-info{
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 6px;
  background: #fafafa;
  text-align: center;
  line-height: 130px;
  font-size: 32px;
  color: #999;
}

.document-add-body-next-btn-box{
  width: 100%;
  padding-top: 20px;
}
.document-add-body-editor-box{
  width: 90%;
  height: auto;
  margin: 0 auto;
}
.full-screen-container {
  z-index: 100;
  border-radius: 6px;
  border: #ebeef5 solid 1px;
}
.document-add-body-editor-box #toolbar-container{
  width: 100%;
  z-index: 102;
  border-bottom: #ebeef5 solid 1px;
}
.document-add-body-editor-box #editor-container{
  width: 100%;
  height: 320px;
  z-index: 101;
}
#editor-container >>> .w-e-text-container{
  min-height: 200px;
  border: none !important;
  overflow: inherit !important;
}
#editor-container >>> .w-e-select-list {
  width: 180px !important;
}
#editor-container >>> pre{
  position: relative;
  background: #f5f2f0;
  margin: 0 0 18px;
  padding: 30px 0 0;
  font-size: 14px;
  border-radius: 4px;
  overflow: hidden;
}
#editor-container >>> pre::before{
  content: '';
  position: absolute;
  top: 9px;
  left: 15px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fc625d;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
  /*z-index: 100;*/
}
#editor-container >>> pre code{
  display: block;
  overflow: auto;
  padding: 0 15px 12px;
  border: none !important;
}
.preview-box{
  width: 100%;
  height: auto;
  padding: 4px 7px;
  border-radius: 6px;
  border: #ebeef5 solid 1px;
}
.preview-box .title{
  font-size: 24px;
  margin: 4px 3px;
  line-height: 2;
  text-align: center;
  border-bottom: #ebeef5 solid 1px;
}
.preview-box >>> h1, .preview-box >>> h2, .preview-box >>> h3,
.preview-box >>> h4, .preview-box >>> h5, .preview-box >>> h6{
  color: var(--color);
  cursor: pointer;
  margin: 5px 0;
}
.preview-box >>> h1::before, .preview-box >>> h2::before, .preview-box >>> h3::before,
.preview-box >>> h4::before, .preview-box >>> h5::before, .preview-box >>> h6::before{
  content: '#';
  padding-right: 10px;
}
.preview-box >>> h1:hover, .preview-box >>> h2:hover, .preview-box >>> h3:hover,
.preview-box >>> h4:hover, .preview-box >>> h5:hover, .preview-box >>> h6:hover{
  color: #007aff;
}
.preview-box >>> img{
  width: 100%;
  height: 100%;
}
.preview-box >>> pre{
  position: relative;
  background: #23241f;
  margin: 0 0 18px;
  padding: 30px 0 0;
  font-size: 14px;
  border-radius: 4px;
  overflow: hidden;
}
.preview-box >>> pre::before{
  content: '';
  position: absolute;
  top: 9px;
  left: 15px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fc625d;
  box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
  z-index: 1;
}
.preview-box >>> pre::after{
  position: absolute;
  top: 6px;
  right: 15px;
  content: 'copy';
  color: #fff;
  cursor: pointer;
  background: #fdbc40;
  padding: 0 4px;
  border-radius: 3px;
  font-size: 12px;
  transition: opacity 0.3s ease-in-out;
}
.preview-box >>> pre code{
  display: block;
  overflow: auto;
  padding: 0 15px 12px;
}
</style>
