<template>
  <div>
    <a-layout id="components-layout-demo-custom-trigger">
      <a-layout-sider v-model="collapsed" :trigger="null" collapsible theme="light">
        <div class="logo"></div>
        <a-menu theme="light" mode="inline" :open-keys="openKeys" @openChange="onOpenChange">

          <a-sub-menu key="sub1">
            <span slot="title"><a-icon type="mail"/><span>首页管理</span></span>
            <a-menu-item key="sub1-1">
              <router-link to="/backend" data-key="sub1-1">
                <a-icon type="android"/> <span>首页文章</span>
              </router-link>
            </a-menu-item>
            <a-menu-item key="sub1-2">
              <a-icon type="android"/> <span>首页文章</span>
            </a-menu-item>
          </a-sub-menu>

          <a-sub-menu key="sub2">
            <span slot="title"><a-icon type="mail"/><span>文章管理</span></span>
            <a-menu-item key="sub2-1">
              <router-link to="/backend/document" data-key="sub2-1">
                <a-icon type="android"/> <span>管理文章</span>
              </router-link>
            </a-menu-item>
            <a-menu-item key="sub2-2">
              <router-link to="/backend/document/list" data-key="sub2-2">
                <a-icon type="android"/> <span>管理列表</span>
              </router-link>
            </a-menu-item>
            <a-menu-item key="sub2-3">
              <router-link to="/backend/document/add" data-key="sub2-3">
                <a-icon type="android"/> <span>添加文章</span>
              </router-link>
            </a-menu-item>
          </a-sub-menu>

          <a-sub-menu key="sub3">
            <span slot="title"><a-icon type="mail"/><span>导航管理</span></span>
            <a-menu-item key="sub3-1">
              <router-link to="/backend/navigation/list" data-key="sub3-1">
                <a-icon type="android"/> <span>导航列表</span>
              </router-link>
            </a-menu-item>
            <a-menu-item key="sub3-2">
              <router-link to="/backend/navigation/add" data-key="sub3-2">
                <a-icon type="android"/> <span>导航添加</span>
              </router-link>
            </a-menu-item>
          </a-sub-menu>

        </a-menu>
      </a-layout-sider>
      <a-layout>
        <a-layout-header style="background: #fff; padding: 0; border-bottom: #ebeef5 solid 1px">
          <a-icon class="trigger" :type="collapsed ? 'menu-unfold' : 'menu-fold'" @click="() => (collapsed = !collapsed)"/>
        </a-layout-header>
        <a-layout-content :style="{padding: '24px', background: '#fff', minHeight: '280px', height: 'auto'}">
          <div class="breadcrumb-box">
            <a-breadcrumb>
              <a-breadcrumb-item>Home</a-breadcrumb-item>
              <a-breadcrumb-item><a href="">Center</a></a-breadcrumb-item>
              <a-breadcrumb-item><a href="">List</a></a-breadcrumb-item>
              <a-breadcrumb-item>Application</a-breadcrumb-item>
            </a-breadcrumb>
          </div>
          <router-view/>
        </a-layout-content>
      </a-layout>
    </a-layout>
  </div>
</template>

<script>
  export default {
    name: "BackendIndex",
    data () {
      return {
        collapsed: false,
        rootSubmenuKeys: ['sub1', 'sub2', 'sub3'],
        openKeys: ['sub1-1'],
      }
    },
    methods: {
      onOpenChange(openKeys) {
        const latestOpenKey = openKeys.find(key => this.openKeys.indexOf(key) === -1);
        if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
          this.openKeys = openKeys;
        } else {
          this.openKeys = latestOpenKey ? [latestOpenKey] : [];
        }
      }
    }
  }
</script>

<style scoped>
#components-layout-demo-custom-trigger{
  position: absolute;
  width: 100%;
  height: 100%;
}
#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}
#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}
#components-layout-demo-custom-trigger .logo {
  width: 80%;
  height: 80px;
  background: rgba(0, 0, 0, 0.2);
  margin: 16px auto;
}
.breadcrumb-box{
  width: 100%;
  margin-bottom: 10px;
}
.ant-layout{
  background: #fff;
}
</style>
