<template>
  <div>

    <backend-title-bar>
      <span slot="title">文章列表</span>

      <a-button slot="action1" type="primary" style="width: 100%;" @click="saveEditDocumenthandler">
        <a-icon type="save"/> <span>保存</span>
      </a-button>

      <a-button slot="action2" type="primary" style="width: 100%;">
        <a-icon type="export"/> <span>导出</span>
      </a-button>
    </backend-title-bar>

    <div class="document-list-body-box">
      <a-table :columns="columns" :data-source="documentList" :row-selection="rowSelection"
               :pagination="pagination" :expanded-row-keys.sync="expandedRowKeys" rowKey="id"
               @change="paginationHandler">

        <div class="list-title-box" slot="title-info" slot-scope="text, record">
          <a-row>
            <a-col :span="6">
              <div class="list-title-img" :class="record.label !== '' ? 'list-title-img-star' : ''">
                <img :src="record.img" alt="" :title="record.tag"/>
              </div>
            </a-col>
            <a-col :span="18">
              <div class="list-title-text">
                <div>{{ record.title }}</div>
                <div>{{ record.abstract }}</div>
              </div>
            </a-col>
          </a-row>
        </div>

        <div slot="tagList" slot-scope="tagList">
          <a-tag color="orange" v-for="(tag, index) in tagList" :key="index">{{ tag }}</a-tag>
        </div>

        <span slot="number" slot-scope="number, record">
          {{ record.action.read }} | {{ record.action.like }} | {{ record.action.comment }}
        </span>

        <span slot="datetime" slot-scope="datetime">
          {{ timestamp(datetime) }}
        </span>

        <div slot="status" slot-scope="status, record">
          <a-select :default-value="record.status ? '1' : '0'" style="width: 100px"
                    @change="changeDocumentStatusHandler(record.id, $event)">
            <a-select-option value="1">
              <span class="docu-status-enable">
                <a-icon type="api" /> <span>启动</span>
              </span>
            </a-select-option>
            <a-select-option value="0">
              <span class="docu-status-disable">
                <a-icon type="api" /> <span>停用</span>
              </span>
            </a-select-option>
          </a-select>
        </div>

        <div slot="action" slot-scope="text, record">
          <a-dropdown-button @click="editDocumentHander(record)">
            <a-icon type="edit"/>
            <a-menu slot="overlay">
              <a-menu-item key="1"><a-icon type="user" />1st menu item </a-menu-item>
              <a-menu-item key="2">
                <a class="document-btn" v-if="record.label !== ''" @click="starDocumentHandler(record.id, '')">
                  <a-icon type="star" theme="twoTone" two-tone-color="#377dff"/> <span>取消推荐</span>
                </a>
                <a class="document-btn" v-else @click="starDocumentHandler(record.id, '推荐')">
                  <a-icon type="star"/> <span>设置为推荐</span>
                </a>
              </a-menu-item>
              <a-menu-item key="3">
                <a class="document-btn" @click="deleteDocumentHandler(record.id)">
                  <a-icon type="delete"/> <span>删除</span>
                </a>
              </a-menu-item>
            </a-menu>
            <a-icon slot="icon" type="down"/>
          </a-dropdown-button>
        </div>
      </a-table>
    </div>

  </div>
</template>

<script>
  import BackendTitleBar from "../../../components/backend/BackendTitleBar";
  import common from "../../../util/common";
  import dataSave from "../../../util/data-save";
  import apiRequest from "../../../request/api/ApiRequest";

  export default {
    name: "BackendDocumentList",
    data () {
      return {
        columns: common.documentListColumns(),
        documentList: [],
        pagination: {current: 1, pageSize: 5, total: 0, type: 1},
        rowSelection: {
          onChange: (selectedRowKeys, selectedRows) => {
            console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
          },
          onSelect: (record, selected, selectedRows) => {
            console.log(record, selected, selectedRows);
          },
          onSelectAll: (selected, selectedRows, changeRows) => {
            console.log(selected, selectedRows, changeRows);
          },
        },
        expandedRowKeys: [],
      }
    },
    mounted() {
      this.paginationHandler(this.pagination)
    },
    methods: {
      paginationHandler (pagination) {
        apiRequest.getDocumentList(
            {page: pagination.current, rows: pagination.pageSize, type: pagination.type})
        .then(res => {
          this.documentList = res.data.list
          this.pagination.total = res.data.total
          this.pagination.current = pagination.current
        })
      },
      timestamp (timestamp) {
        return common.timestamp(timestamp)
      },
      changeDocumentStatusHandler (id, value) {
        for (const document of this.documentList) {
          if (document.id === id) {
            document.status = value === '1'
            break
          }
        }
      },
      saveEditDocumenthandler () {
        dataSave.saveEditData(this.documentList)
        this.paginationHandler({current: 1, pageSize: 5, total: 0, type: 1})
      },
      editDocumentHander (document) {
        this.$message.info('编辑文档')
        console.log(document)
      },
      starDocumentHandler (id, lable) {
        for (const document of this.documentList) {
          if (document.id === id) {
            document.label = lable
            break
          }
        }
      },
      deleteDocumentHandler (id) {
        console.log(id)
      }
    },
    components: { BackendTitleBar }
  }
</script>

<style scoped>
.document-list-body-box{
  width: 100%;
  height: auto;
  margin-top: .8rem;
}
.document-btn{
  display: block;
  font-size: 14px;
}
.document-btn > span{
  display: inline-block;
  margin-left: 10px;
}
.list-title-box{
  width: 260px;
}
.list-title-img{
  width: 50px;
  height: 50px;
  padding: 2px;
  border-radius: 50%;
  border: #d9d9d9 solid 3px;
}
.list-title-img-star {
  border-color: #2db7f5;
}
.list-title-img img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.list-title-text{
  width: 210px;
  height: 50px;
}
.list-title-text div{
  width: 210px;
  height: 25px;
  color: #677788;
  font-size: .8125rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.list-title-text div:first-child{
  color: #1e2022 !important;
  font-size: .875rem !important;
}
.docu-status-enable{
  color: #40a9ff;
}
.docu-status-disable{
  color: #ff4d4f;
}
</style>
