<script>
  import BackendTitleBar from "../../../components/backend/BackendTitleBar";
  import apiRequest from "../../../request/api/ApiRequest";
  import common from "../../../util/common";

  export default {
    name: "BackendDocument",
    data () {
      return {
        recomDocumentList: [],
        recomPagination: {page: 1, rows: 4},
        tagList: [],
        documentStatistics: {document: 0, tag: 0, recom: 0, datetime: 0}
      }
    },
    mounted() {
      apiRequest.getTagList().then(res => {
        if (res.code === 2000) {
          this.tagList = res.data
        }
      })
      apiRequest.getRecomDocumentList(this.recomPagination).then(res => {
        if (res.code === 2000) {
          this.recomDocumentList = res.data
        }
      })
      apiRequest.getDocumentStatistics().then(res => {
        if (res.code === 2000) {
          this.documentStatistics = res.data
        }
      })
    },
    methods: {
      timestamp (timestamp) {
        return common.timestamp(timestamp)
      }
    },
    components: { BackendTitleBar }
  }
</script>

