<template>
  <div>

    <div class="document-main-load-box" v-show="loadingStatus">
      <a-spin size="large" tip="玩命加载中..."/>
    </div>

    <div class="document-main-box" v-show="!loadingStatus">

      <a-row :gutter="[16,16]">
        <a-col :span="5">
          <div class="main-card-item-box catalogue">
            <main-card-bar>
              <span slot="title">目录</span>
              <catalogue-bar slot="content" :catalogue="documentInfo.catalogue"/>
            </main-card-bar>
          </div>
        </a-col>
        <a-col :span="19">
          <div class="document-main-right-box">
            <div class="document-main-content-box">
              <div class="document-main-header">
                <h1>{{ documentInfo.title }}</h1>
                <p>{{ documentInfo.abstract }}</p>
              </div>
              <div class="document-main-content" v-highlight v-html="documentInfo.content"></div>
            </div>
          </div>
        </a-col>
      </a-row>
    </div>

  </div>
</template>

<script>
import apiRequest from "../../../request/api/ApiRequest";
import common from "../../../util/common";
import editorJs from '../../../assets/editor-js'
import MainCardBar from "../../../components/frontend/main/MainCardBar";
import CatalogueBar from "../../../components/frontend/document/CatalogueBar";

import('../../../assets/editor-style.css')
export default {
  name: "DocumentMain",
  data () {
    return {
      loadingStatus: false,
      documentInfo: {action: {like: 0, read: 0, comment: 0}}
    }
  },
  mounted() {
    this.loadingStatus = true
    apiRequest.getDocumentDetails(this.$route.params.id).then(res => {
      if (res.code === 2000) {
        this.documentInfo = res.data
      }
      this.loadingStatus = false
    })

    // eslint-disable-next-line no-undef
    $(".document-main-content").on("click", "img", (item) => {
      // eslint-disable-next-line no-undef
      editorJs.showBigImg($(item.currentTarget).attr('src'))
    })
  },
  methods: {
    timestampShow (timestamp) {
      return common.timestampShow(timestamp)
    },
    simpleTimestamp (timestamp) {
      return common.simpleTimestamp(timestamp)
    }
  },
  components: { MainCardBar, CatalogueBar }
}
</script>
<style scoped>
.document-main-load-box{
  position: fixed;
  width: 100%;
  height: 100vh;
  text-align: center;
  padding-top: 10rem;
}

.document-main-box{
  width: 100%;
  height: auto;
  padding: 2em;
  background: #EBF4FF;
}
.document-main-box .main-card-item-box{
  margin-bottom: 1em;
}
.document-main-box .document-main-content-box{
  width: 100%;
  background: #fff;
  border-radius: 2px;
}
.document-main-box .document-main-content-box .document-main-header{
  color: #333333;
  padding: 2em 1em;
}
.document-main-box .document-main-content-box .document-main-header h1{
  color: #333333;
}
.document-main-box .document-main-content-box .document-main-header p{
  padding: 16px;
  margin: 0 0 24px;
  border-left: 8px solid #dddfe4;
  background: #eef0f4;
}
.document-main-box .document-main-content-box .document-main-content{
  width: 100%;
  height: auto;
  padding: 1rem;
}
.document-main-box .document-main-content-box .document-main-content >>> img{
  width: 100%;
  height: 100%;
  cursor: zoom-in;
}
</style>
