import Vue from 'vue'
import Router from 'vue-router'

import IndexView from "../page/frontend/IndexView";
import IndexMain from "../page/frontend/main/IndexMain";
import DocumentMain from "../page/frontend/document/DocumentMain";

import BackendIndex from "../page/backend/BackendIndex";
import BackendLogin from "../page/backend/login/BackendLogin";
import BackendMain from "../page/backend/main/BackendMain";
import BackendDocument from "../page/backend/document/BackendDocument";
import BackendDocumentList from "../page/backend/document/BackendDocumentList";
import BackendAddDocument from "../page/backend/document/BackendAddDocument";
import BackNavigationAdd from "../page/backend/navigation/BackNavigationAdd";
import BackNavigationList from "../page/backend/navigation/BackNavigationList";

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/',
      name: 'IndexView',
      component: IndexView,
      meta: {auth: false},
      children: [
        {
          path: '/',
          name: 'IndexMain',
          component: IndexMain,
          meta: {auth: false},
        },
        {
          path: '/document/:id',
          name: 'DocumentMain',
          component: DocumentMain,
          meta: {auth: false},
        }
      ]
    },
    {
      path: '/backend/login',
      name: 'BackendLogin',
      component: BackendLogin,
      children: []
    },
    {
      path: '/backend',
      name: 'BackendIndex',
      component: BackendIndex,
      children: [
        {path: '/', name: 'BackendMain', component: BackendMain},
        {path: '/backend/document', name: 'BackendDocument', component: BackendDocument},
        {path: '/backend/document/list', name: 'BackendDocumentList', component: BackendDocumentList},
        {path: '/backend/document/add', name: 'BackendAddDocument', component: BackendAddDocument},
        {path: '/backend/navigation/add', name: 'BackNavigationAdd', component: BackNavigationAdd},
        {path: '/backend/navigation/list', name: 'BackNavigationList', component: BackNavigationList}
      ]
    }
  ]
})

// eslint-disable-next-line no-unused-vars
router.beforeEach((to, from, next) => {
  document.body.scrollTop = 0
  document.documentElement.scrollTop = 0
  window.pageYOffset = 0
  next()
})

