let documentList = []

const dataCache = {

  addDocumentList: (documents) => {
    for (const document of documents) {
      documentList.push(document)
    }
    sessionStorage.setItem('luochen-document-list', JSON.stringify(documentList))
  },

  getDocumentList () {
    if (documentList.length < 1) {
      documentList = JSON.parse(sessionStorage.getItem('luochen-document-list'))
    }
    return documentList
  },

  cleanDocumentList () {
    documentList = []
    sessionStorage.removeItem('luochen-document-list')
  }

}

export default dataCache
